import React from 'react';
import logo from './Icons/logo-mw-2x.png';
import firebase from './firebase/firebase';
import * as ROUTES from './routes/routes';
import {localStorageName} from './CompareSite/Compare';
import {useHistory} from 'react-router';

import {
    AppBar,
    Toolbar,
    Button,
    Grid,
} from '@material-ui/core';

const Bar = () => {
    const history = useHistory();

    return(
        <AppBar>
            <Toolbar style={{backgroundColor: 'rgb(40, 40, 40)'}}>
                <Grid container spacing={0} alignItems='center'>
                    <Grid item xs={4}>
                        <Button 
                            onClick={() => {
                                localStorage.setItem(localStorageName, JSON.stringify([]));
                                history.push(ROUTES.HOME);
                            }}
                        >
                            <img alt="MaibornWolff" height="50" src={logo} />
                        </Button>
                    </Grid>
               
                    {firebase.getCurrentUsername() ? (
                        <>
                            <Grid item xs={2}>
                                <Button 
                                    style={{color: 'white'}}
                                    onClick={() => {
                                        localStorage.setItem(localStorageName, JSON.stringify([]));
                                        history.push(ROUTES.COMPARE)
                                    }}
                                >
                                    Compare
                                </Button>
                            </Grid>
                                
                            <Grid item xs={2}>
                                <Button
                                    style={{color: 'white'}} 
                                    onClick={() => {
                                        firebase.logout();
                                        history.push(ROUTES.LOGIN)
                                    }}
                                >
                                    Logout
                                </Button>
                            </Grid>
                            
                            <Grid item md={1} lg={2}></Grid>

                            <Grid item md={3} lg={2}>
                                <div style={{fontFamily: 'sans-serif'}}>
                                    Logged in as: {firebase.getCurrentUsername()}
                                </div>
                            </Grid> 
                        </>
                    ): null}
                 </Grid>
            </Toolbar>
        </AppBar>
    )
}


export default Bar;