import axios from 'axios';
import {firebase} from './firebase/firebase';

const npmIoApiUrl = 'https://api.npms.io/v2/package/';
const FirebaseDatabaseName = 'Libraries';

export const libraryRef = firebase.database().ref(FirebaseDatabaseName);

export const prettyNumber = (number, operator) => {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, operator);
}

export const checkDuplicateEntry = (arr, searchValue) => {
    return (arr.some(el => el.name === searchValue));
}

export const compareByFinalScore = (a, b) => {
    if (a.finalScore < b.finalScore) return 1;
    if (a.finalScore > b.finalScore) return -1;
    return 0;
};
  
export const compareByName = (a, b) => {
    if (a.name.toLowerCase() > b.name.toLowerCase()) return 1;
    if (a.name.toLowerCase() < b.name.toLowerCase()) return -1;
    return 0;
};
  
export const compareByDownloads = (a, b) => {
    if (a.downloads < b.downloads) return 1;
    if (a.downloads > b.downloads) return -1;
    return 0;
};
  
export const compareByStars = (a, b) => {
    if (a.stars < b.stars) return 1;
    if (a.stars > b.stars) return -1;
    return 0;
};

export const buildListOfPinnedLibrariesFromLocalStorage = (localStorageName, listOfChoosenLibraries) => {
    const test = JSON.parse(localStorage.getItem(localStorageName));
    const arr = [];

    test.forEach((testE) => {
        if(listOfChoosenLibraries.some(listE => listE.name === testE.name)) arr.push(testE);
    })
    return arr;
}

export const buildListOfChoosenLibrariesFromURL = (searchedUrlPackages, listOfAllLibraries) => {
    const selectedPackages = searchedUrlPackages.split('?query=')[1].split(',');
    const listOfChoosenLibraries = selectedPackages.map(currSelected => {
        const index = listOfAllLibraries.map(e => {return e.name}).indexOf(currSelected);
        return index !== -1 ? listOfAllLibraries[index] : null;
    }).filter((e) => {
        return e !== null;
    })
    return listOfChoosenLibraries;
}

export async function searchNewPackages(searchLibrary) {
    return new Promise(function(resolve) {
        axios.get(npmIoApiUrl+searchLibrary)
            .then(response => {
                const apiData = response.data;
                const metaData = apiData.collected.metadata;
                const npmData = apiData.collected.npm;
                const githubData = apiData.collected.github;
                const libraryDataObject = {};

                if (metaData.name) libraryDataObject.name = metaData.name;
                if (metaData.version) libraryDataObject.version = metaData.version;
                if (metaData.description) libraryDataObject.description = metaData.description;
                if (metaData.license) libraryDataObject.license = metaData.license;
                if (npmData.downloads) libraryDataObject.downloads = npmData.downloads[1].count;
                if (metaData.dependencies) libraryDataObject.dependencies = Object.keys(metaData.dependencies);
                if (githubData?.stars) libraryDataObject.stars = githubData.stars;
                if (githubData?.openIssues) libraryDataObject.openIssues = githubData.issues.openCount;
                if (apiData.collected.source) libraryDataObject.fileSize = apiData.collected.source.files.testsSize;
                if (metaData.releases) libraryDataObject.releases = metaData.releases[0].count;
                if (metaData.date) libraryDataObject.creationDate = metaData.date;
                if (metaData.links.npm) libraryDataObject.npmLink = metaData.links.npm;
                if (metaData.links.repository) libraryDataObject.gitLink = metaData.links.repository;
                if (apiData.score.final) libraryDataObject.finalScore = apiData.score.final;

                libraryRef.push(libraryDataObject);
                resolve('done');

            }).catch(err => {
                resolve(err);
                console.log(searchLibrary + ' not found');
                console.error(err);
            })      
    })
}