import React, {useCallback, useState, useEffect} from 'react';
import TablePaginatoinActions from './TablePagination';
import Bar from './Bar';
import Link from '@material-ui/core/Link';
import Footer from './Footer';
import * as ROUTES from './routes/routes';
import firebase from './firebase/firebase';
import {useHistory} from 'react-router';
import {compareByName, compareByFinalScore, libraryRef} from './funktions';

import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Button,
    TableFooter,
    TablePagination,
    Tooltip,
    Checkbox,
    Menu,
    MenuItem,
    Toolbar,
    Typography,
    Box,
    Dialog,
    DialogTitle,
    DialogActions,
    DialogContent,
    IconButton,
    TextField,
    InputAdornment,
    LinearProgress,
    Snackbar
} from '@material-ui/core';

import MuiAlert from '@material-ui/lab/Alert';

import {
    FilterList as FilterListIcon,
    Search as SearchIcon,
} from '@material-ui/icons';

function filterSummary(arr, searchKey) {
    const reg = new RegExp(searchKey, 'i');
    return arr.filter(curr => curr.name.match(reg) || curr.description?.match(reg));
}

const goToCompare = (history, selectedPackages) => {
    if(selectedPackages.length > 0) {
        history.push(`/compare?query=${selectedPackages.join(',')}`);
        return;
    }

    history.push(ROUTES.COMPARE);
}

const Alert = (props) => {
    return <MuiAlert elevation={6} variant='filled' {...props} />
}

const Home = () => {
    const history = useHistory();
    const filterOptions = ['show only selected packages', 'sort by Score', 'sort by Name'];

    const [listOfAllLibraries, setListOfAllLibraries] = useState([]);
    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(20);
    const [filterButtonAnchor, setFilterButtonAnchor] = useState(null);
    const [selectedPackages, setSelectedPackages] = useState([]);
    const [showSelectedPackages, setShowSelectedPackages] = useState(false);
    const [noSelectedPackages, setNoSelectedPackages] = useState(false);
    const [loading, setLoading] = useState(true);
    const [searchFieldInput, setSearchFieldInput] = useState('');
    const [filteredLibraries, setFilteredLibraries] = useState([]);
    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [clipboardAnswer, setClipboardAnswer] = useState('error');

    const emptyRows = rowsPerPage - Math.min(rowsPerPage, filteredLibraries.length - page * rowsPerPage);

    useEffect(() => {
        if(!firebase.getCurrentUsername()) history.push(ROUTES.LOGIN);

        libraryRef.on('value', (snapshot) => {
            const libraries = snapshot.val();
            const allLibraryList = [];
      
            for(let id in libraries) {
              allLibraryList.push(libraries[id])
            }
            allLibraryList.sort(compareByName);
            setListOfAllLibraries(allLibraryList);
            setFilteredLibraries(allLibraryList);
            setLoading(false);
        });
    }, [history]);

    const handleChangeRowsPerPage = useCallback((event) => {
        setRowsPerPage(parseInt(event.target.value));
        setPage(0);
    }, []);

    const handleTableClick = useCallback((event, name) => {
        const selectedIndex = selectedPackages.indexOf(name);
        let newSelected = [];

        switch(selectedIndex) {
            case -1:
                newSelected = newSelected.concat(selectedPackages, name);
                break;
            case 0:
                newSelected = newSelected.concat(selectedPackages.slice(1));
                break;
            case selectedPackages.length - 1:
                newSelected = newSelected.concat(selectedPackages.slice(0, -1));
                break;
            default:
                newSelected = newSelected.concat(
                    selectedPackages.slice(0, selectedIndex),
                    selectedPackages.slice(selectedIndex + 1),
                );
        }

        setSelectedPackages(newSelected);
    }, [selectedPackages]);

    const handleSearchFieldChange = useCallback((event) => {
        if(!event) event = {target: {value: ''}};
        setSearchFieldInput(event.target.value);
        setFilteredLibraries(filterSummary(listOfAllLibraries, event.target.value));
    }, [listOfAllLibraries]);

    const handleShowSelectedPackagesButton = useCallback(() => {
        if(!showSelectedPackages) {
            const arr = [];
            selectedPackages.forEach((currSelected) => {
                const index = listOfAllLibraries.map(currPackage => {return currPackage.name}).indexOf(currSelected);
                arr.push(listOfAllLibraries[index]);
            })

            if(arr.length) {
                setFilteredLibraries(arr);
                setPage(0);
            }else {
                setSnackbarOpen(true);
                setClipboardAnswer('error');
            }
        }
        else setFilteredLibraries(listOfAllLibraries);
        setShowSelectedPackages(!showSelectedPackages);
    }, [showSelectedPackages, listOfAllLibraries, selectedPackages]);

    const handleMenuClose = useCallback((event, index) => {
        switch(index) {
            case 0:
                handleShowSelectedPackagesButton();
                break;
            case 1:
                setFilteredLibraries([...filteredLibraries.sort(compareByFinalScore)]);
                break;
            case 2:
                setFilteredLibraries([...filteredLibraries.sort(compareByName)]);
                break;
            default:
                console.error('something went wrong with the menu')
        }

        setFilterButtonAnchor(null);
    }, [filteredLibraries, handleShowSelectedPackagesButton]);

    const handleCompareButton = useCallback(() => {
        if (selectedPackages.length > 0) {
            goToCompare(history, selectedPackages);
            return;
        }
        setNoSelectedPackages(true);
    }, [history, selectedPackages]);

    const handleSelectAll = useCallback((event) => {
        if(event.target.checked) {
            const newSelected = filteredLibraries.map(curr => curr.name);
            setSelectedPackages(newSelected);
            return;
        }
        setSelectedPackages([]);
    },[filteredLibraries]);

    const handleCloseSnackbar = (event, reason) => {
        if (reason === 'clickaway') return;
        setSnackbarOpen(false);
    }   

    const isItemSelected = (name) => selectedPackages.indexOf(name) !== -1;

    return (
        <>
            <Bar />

            <Dialog open={noSelectedPackages} onClose={() => setNoSelectedPackages(false)}>
                <DialogTitle>No packages selected</DialogTitle>
                <DialogContent>Do you really want to go to <b>compare</b>?</DialogContent>
                <DialogActions>
                    <Button onClick={() => setNoSelectedPackages(false)}>
                        Abbrechen
                    </Button>
                    <Button onClick={() => goToCompare(history, selectedPackages)}>
                        Weiter
                    </Button>
                </DialogActions>
            </Dialog>

            {loading ? (
                <LinearProgress />
            ):(
                <div style={{margin: '100px 10%'}}>
                    <Paper style={{backgroundColor: 'rgb(250, 240, 240)'}}>
                        <Toolbar style={{float: 'right'}}>
                            <TextField
                                style={{marginRight: '50px'}}
                                placeholder='search packages'
                                value={searchFieldInput}
                                onChange={handleSearchFieldChange}
                                InputProps={{
                                    startAdornment: (
                                        <InputAdornment position="start">
                                            <SearchIcon />
                                        </InputAdornment>
                                    ),
                                }}
                            />

                            <Tooltip title='filter table'>
                                <IconButton 
                                    color='primary' 
                                    onClick={event => setFilterButtonAnchor(event.currentTarget)}
                                    size='medium'
                                    style={{margin: 20}}
                                >
                                    <FilterListIcon />
                                </IconButton>
                            </Tooltip>
                            <Menu
                                anchorEl={filterButtonAnchor}
                                keepMounted
                                open={Boolean(filterButtonAnchor)}
                                onClose={handleMenuClose}
                            >
                                {filterOptions.map((option, index) => (
                                    <MenuItem 
                                        key={index}
                                        onClick={event => handleMenuClose(event, index)}
                                    >
                                        {index === 0 && showSelectedPackages ? 'show all packages' : option}
                                    </MenuItem>
                                ))}
                            </Menu>

                            <Tooltip title='compare selected Packages'>
                                <Button
                                    variant='outlined'
                                    color='primary'
                                    onClick={handleCompareButton}
                                    style={{margin: 20}}
                                >
                                    compare choosen packages
                                </Button>
                            </Tooltip>
                        </Toolbar>
                        
                        <TableContainer style={{backgroundColor: 'white'}}>
                            <Table>
                                <colgroup>
                                    <col width='10%' />
                                    <col width='50%' />
                                    <col width='5%' />
                                </colgroup>
                                <TableHead>
                                    <TableRow>
                                        <TableCell>
                                            <Tooltip title='select all'>
                                                <Checkbox 
                                                    checked={filteredLibraries.length > 0 && selectedPackages.length === filteredLibraries.length}
                                                    onChange={handleSelectAll}
                                                />
                                            </Tooltip>
                                            Name
                                        </TableCell>
                                        <TableCell align='center'>Description</TableCell>
                                        <TableCell align='center'>Final Score</TableCell>
                                    </TableRow>
                                </TableHead>

                                <TableBody>
                                    {filteredLibraries.length > 0 ? (
                                        rowsPerPage > 0 
                                            ? filteredLibraries.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                            : filteredLibraries
                                        ).map((curr, index) => (
                                            <TableRow 
                                                key={index}
                                                hover
                                                onClick={(event => handleTableClick(event, curr.name))}
                                                selected={isItemSelected(curr.name)}
                                            >
                                                <TableCell>
                                                    <Checkbox checked={isItemSelected(curr.name)} /> 
                                                    {curr.name}
                                                </TableCell>
                                                <TableCell>{curr.description}</TableCell>
                                                <TableCell align='right'>{Number((curr.finalScore).toFixed(3))}</TableCell>
                                            </TableRow>
                                        ),

                                        emptyRows > 0 && (
                                            <TableRow style={{height: 53 * emptyRows}}>
                                                <TableCell colSpan={3} />
                                            </TableRow>
                                        )
                                    ) : (
                                        searchFieldInput ? (
                                            <TableRow>
                                                <TableCell colSpan={3} align='center'>
                                                    <Typography variant='h5'>
                                                        <Box fontWeight='fontWeightBold'>
                                                            No packages found based on your querry, please go to the <Link onClick={() => history.push(ROUTES.COMPARE)}>Compare Page</Link> and search for this package
                                                        </Box>
                                                    </Typography>
                                                </TableCell>
                                            </TableRow>
                                        ) : null)}
                                </TableBody>

                                <TableFooter style={{backgroundColor: 'rgb(250, 240, 240)'}}>
                                    <TableRow>
                                        <TablePagination
                                            rowsPerPageOptions={[20, 50, 100, {label: 'All', value: -1}]}
                                            colSpan={3}
                                            count={filteredLibraries.length}
                                            rowsPerPage={rowsPerPage}
                                            page={page}
                                            SelectProps={{
                                                native: true,
                                            }}
                                            onChangePage={(event, newPage) => setPage(newPage)}
                                            onChangeRowsPerPage={handleChangeRowsPerPage}
                                            ActionsComponent={TablePaginatoinActions}
                                        />
                                    </TableRow>
                                </TableFooter>
                            </Table>
                        </TableContainer>
                    </Paper>

                    <Tooltip title='compare selected Packages'>
                        <Button
                            style={{marginTop: '3%', marginBottom: '5%', float: 'right'}}
                            variant='outlined'
                            color='primary'
                            onClick={handleCompareButton}
                        >
                            compare packages
                        </Button>
                    </Tooltip>
                </div>
            )}

            <Snackbar open={snackbarOpen} autoHideDuration={5000} onClose={handleCloseSnackbar}>
                <Alert onClose={handleCloseSnackbar} severity={clipboardAnswer}>
                    No Packages selected
                </Alert>
            </Snackbar>

            <Footer />
        </>
    )
}

export default Home;