import React, { useState } from 'react';
import {prettyNumber} from '../funktions';

import {
    Card,
    CardHeader,
    Tooltip,
    IconButton,
    CardContent,
    Grid,
    Typography,
    Box,
    CardActions,
    Button,
    Chip,
    Snackbar,
    Accordion,
    AccordionSummary,
    AccordionDetails,
    List,
    ListItem,
    makeStyles,
} from '@material-ui/core';

import MuiAlert from '@material-ui/lab/Alert';

import {
    DeleteForever as DeleteIcon,
    GitHub as GitHubIcon,
    Star as StarIcon,
    StarBorder as DontFilledStarIcon,
    GetApp as DownloadIcon,
    BugReport as OpenIssuesIcon,
    NewReleases as RealeasesIcon,
    Description as DescriptionIcon,
    ShowChart as ScoreIcon,
    FileCopyOutlined as CopyIcon,
    ExpandMore as ExpandMoreIcon,
    OfflineBolt as DependencieIcon,
} from '@material-ui/icons';

const useStyles = makeStyles({
    hideBorder: {
      '&.MuiPaper-elevation1': {
        boxShadow: 'none',
      },
      '&.MuiAccordion-root:before': {
          backgroundColor: 'white',
      },
    },
    pinedLibrary: {
        backgroundColor: 'rgb(250, 240, 240)',
        '&.MuiAccordion-root:before': {
            backgroundColor: 'rgb(250, 240, 240)',
        },
    },
})

const getCardSubheader = (date, version, license, filesize) => {
    const releaseDate = new Date(date).toLocaleDateString();

    return (
        <Grid container>
            <Grid item xs={6}>
                <Typography variant='body1'>
                    Published: {releaseDate || '-'} <br />
                    Version: {version ? version : '-'}
                </Typography>
            </Grid>
            <Grid item xs={1} />
            <Grid item xs={5}>
                <Typography variant='body1'>
                    License: {license || '-'} <br />
                    Filesize: {filesize ? prettyNumber(filesize, ',') + ' kB' : '-'}
                </Typography>
            </Grid>
        </Grid>
    )
}

const Alert = (props) => {
    return <MuiAlert elevation={6} variant='filled' {...props} />
}

const CompareCard = ({cardData, dropLibraryFunc, pined, pinFunc}) => {
    const classes = useStyles();
    const copyNpmDownloadLink = 'npm i ' + cardData.name;
    const copyYarnDownloadLink = 'yarn add ' + cardData.name;

    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [clipboardAnswer, setClipboardAnswer] = useState('error');

    const handleChipClick = (copyLink) => {
        navigator.clipboard.writeText(copyLink).then(function() {
            setSnackbarOpen(true);
            setClipboardAnswer('success');
        }, function() {
            setSnackbarOpen(true);
            setClipboardAnswer('error');
        })
    }

    const handleCloseSnackbar = (event, reason) => {
        if (reason === 'clickaway') return;
        setSnackbarOpen(false);
    }

    return (
        <>
            <Card 
                variant='outlined' 
                className={pined ? classes.pinedLibrary : ''}
            >
                <CardHeader 
                    title={cardData.name}
                    subheader={getCardSubheader(cardData.creationDate, cardData.version, cardData.license, cardData.fileSize)}
                    action={
                        <>
                            {pined ? (
                                <Tooltip title='dont pin library'>
                                    <IconButton onClick={pinFunc} color='primary'>
                                        <StarIcon style={{color: 'gold'}} />
                                    </IconButton>
                                </Tooltip>
                            ):(
                                <Tooltip title='pin library'>
                                    <IconButton onClick={pinFunc} color='primary'>
                                        <DontFilledStarIcon />
                                    </IconButton>
                                </Tooltip>
                            )}

                            <Tooltip title='drop library'>
                                <IconButton onClick={dropLibraryFunc} style={{color: 'rgb(180, 0, 0)'}}>
                                    <DeleteIcon />
                                </IconButton>
                            </Tooltip>
                        </>
                    }
                />

                <CardContent>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Typography variant='body1' component='div'>
                                <Tooltip title='Description'>
                                    <DescriptionIcon style={{height: 15}}/>
                                </Tooltip>
                            </Typography>
                            <Typography variant='body2' style={{wordWrap: 'break-word', marginLeft: '15px', marginRight: '15px'}}>
                                {cardData.description}
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant='body1' component='div'>
                                <Box fontWeight='fontWeightBold'>
                                    <DownloadIcon style={{height: 15}}/>Downloads last week:
                                </Box>
                            </Typography>
                            <Typography variant='body2' style={{marginLeft: '15px'}}>
                                {cardData.downloads >= 0 ? prettyNumber(cardData.downloads, '.') : '-'}
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant='body1' component='div'>
                                <Box fontWeight='fontWeightBold'>
                                    <StarIcon style={{height: 15}}/>Stars on GitHub:
                                </Box>
                            </Typography>
                            <Typography variant='body2' style={{marginLeft: '15px'}}>
                                {cardData.stars >= 0 ? prettyNumber(cardData.stars, '.') : '-'}
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant='body1' component='div'>
                                <Box fontWeight='fontWeightBold'>
                                    <DependencieIcon style={{height: 15}}/>Dependencies:
                                </Box>
                            </Typography>
                        
                            {cardData.dependencies ? (
                                <Accordion 
                                    className={pined ? [classes.hideBorder, classes.pinedLibrary].join(' ') : classes.hideBorder}
                                    style={{fontFamily: 'sans-serif'}}
                                    >
                                    <Tooltip title='show depency names'>
                                        <AccordionSummary style={{height: 15}} expandIcon={<ExpandMoreIcon />}>
                                            {cardData.dependencies.length}
                                        </AccordionSummary>
                                    </Tooltip>
                                    <AccordionDetails>
                                        <List component='nav'>
                                            {cardData.dependencies.map(curr =>
                                                <ListItem key={curr}>
                                                    {curr}
                                                </ListItem>
                                            )}
                                        </List>
                                    </AccordionDetails>
                                </Accordion>
                            ) : (
                                <Typography variant='body2' style={{marginLeft: '15px'}}>
                                    -
                                </Typography>
                            )}
                        
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant='body1' component='div'>
                                <Box fontWeight='fontWeightBold'>
                                    <OpenIssuesIcon style={{height: 15}}/>Open Issues:
                                </Box>
                            </Typography>
                            <Typography variant='body2' style={{marginLeft: '15px'}}>
                                {cardData.openIssues >= 0 ? cardData.openIssues : '-'}
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant='body1' component='div'>
                                <Box fontWeight='fontWeightBold'>
                                    <RealeasesIcon style={{height: 15}}/>Releases in the last month:
                                </Box>
                            </Typography>
                            <Typography variant='body2' style={{marginLeft: '15px'}}>
                                {cardData.releases >= 0 ? cardData.releases : '-'}
                            </Typography>
                        </Grid>

                        <Grid item xs={6}>
                            <Typography variant='body1' component='div'>
                                <Box fontWeight='fontWeightBold'>
                                    <ScoreIcon style={{height: 15}}/>Finalscore:
                                </Box>
                            </Typography>
                            <Typography variant='body2' style={{marginLeft: '15px'}}>
                                {Number((cardData.finalScore).toFixed(3))}
                            </Typography>
                        </Grid>
                    </Grid>
                </CardContent>

                <CardActions>
                    {cardData.gitLink ? (
                        <Tooltip title='show library on GitHub'>
                            <IconButton color='primary' onClick={() => window.open(cardData.gitLink, '_blank')}>
                                <GitHubIcon />
                            </IconButton>
                        </Tooltip>
                    ): null}

                    {cardData.npmLink ? (
                        <Tooltip title='show library on npm'>
                            <Button color='primary' onClick={() => window.open(cardData.npmLink, '_blank')}>
                                npm
                            </Button>
                        </Tooltip>
                    ): null}
                </CardActions>
                <CardActions> 
                    <Tooltip title='copy command to clipboard'>
                        <Chip
                            label={copyNpmDownloadLink}
                            variant='outlined'
                            onClick={() => handleChipClick(copyNpmDownloadLink)}
                            onDelete={() => handleChipClick(copyNpmDownloadLink)}
                            deleteIcon={<CopyIcon />}
                        />
                    </Tooltip>

                    <Tooltip title='copy command to clipboard'>
                        <Chip
                            label={copyYarnDownloadLink}
                            variant='outlined'
                            onClick={() => handleChipClick(copyYarnDownloadLink)}
                            onDelete={() => handleChipClick(copyYarnDownloadLink)}
                            deleteIcon={<CopyIcon />}
                        />
                    </Tooltip>
                </CardActions>
            </Card>

            <Snackbar open={snackbarOpen} autoHideDuration={5000} onClose={handleCloseSnackbar}>
                <Alert onClose={handleCloseSnackbar} severity={clipboardAnswer}>
                    Link copied to clipboard
                </Alert>
            </Snackbar>
        </>
    )
}

export default CompareCard;