import React from 'react';
import { Snackbar } from '@material-ui/core';
import { Alert as MuiAlert } from '@material-ui/lab';

const Alert = (props) => {
    return <MuiAlert elevation={6} variant='filled' {...props} />
}

const ErrorSnackbar = ({open, severty, message, handleCloseSnackbar}) => {

    return (
        <Snackbar open={open} autoHideDuration={5000} onClose={(event, reason) => handleCloseSnackbar(reason)}>
            <Alert onClose={handleCloseSnackbar} severity={severty}>
                {message}
            </Alert>
        </Snackbar>
    )
}

export default ErrorSnackbar;