import React, {useCallback, useState, useEffect} from 'react';
import {useLocation, useHistory} from 'react-router';
import CompareCard from './CompareCard';
import Bar from '../Bar';
import Footer from '../Footer';
import ErrorSnackbar from '../ErrorSnackbar';
import firebase from '../firebase/firebase';
import * as ROUTES from '../routes/routes';
import {
  compareByFinalScore, 
  compareByName, 
  compareByDownloads, 
  compareByStars, 
  searchNewPackages,
  buildListOfChoosenLibrariesFromURL,
  buildListOfPinnedLibrariesFromLocalStorage,
  libraryRef,
  checkDuplicateEntry,
} from '../funktions';

import {
  TextField,
  Typography,
  IconButton,
  Tooltip,
  Box,
  Menu,
  MenuItem,
  Grid,
  makeStyles,
  LinearProgress,
} from '@material-ui/core';

import {Autocomplete} from '@material-ui/lab';

import {FilterList as FilterListIcon} from '@material-ui/icons';

const useStyle = makeStyles((theme) => ({
  footer: {
      marginLeft: '-10%',
      marginRight: '-10%',
      position: 'absolute',
      bottom: 0,
      width: '98%',
      height: '60px',
  }
}));

const selectedPackagesForURL = (listOfSelectedPackages, history) => {
  if (listOfSelectedPackages.length === 0) history.push(ROUTES.COMPARE);

  const list = listOfSelectedPackages?.map(curr => curr.name);
  history.push(`/compare?query=${list.join(',')}`);
}

export const localStorageName = 'pinedLibraries';

const Compare = () => {
  const classes = useStyle();
  const searchedUrlPackages = useLocation().search;
  const history = useHistory();

  const filterOptions = ['sort by Score', 'sort by Name', 'sort by Downloads', 'sort by stars'];

  const [listOfAllLibraries, setListOfAllLibraries] = useState([]);
  const [filterButtonAnchor, setFilterButtonAnchor] = useState(null);
  const [listOfSearchedLibraries, setListOfSearchedLibraries] = useState([]);
  const [listOfPinedLibraries, setListOfPinedLibraries] = useState([]);
  const [duplicateEntry, setDuplicateEntry] = useState(false);
  const [loading, setLoading] = useState(false);
  const [currentSearchFieldValue, setCurrentSearchFieldValue] = useState('');
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');
  const [snackbarSeverity, setSnackbarSeverity] = useState('error');

  useEffect(() => {
    if(!firebase.getCurrentUsername()) history.push(ROUTES.LOGIN);

    libraryRef.on('value', (snapshot) => {
      const libraries = snapshot.val();
      const allLibraryList = [];

      for(let id in libraries) {
        allLibraryList.push(libraries[id])
      }
      allLibraryList.sort(compareByName);
      setListOfAllLibraries(allLibraryList);

      if(searchedUrlPackages === '?query=') {
        localStorage.setItem(localStorageName, JSON.stringify([]));
        history.push(ROUTES.COMPARE);
      } 
      if(searchedUrlPackages) {
        const searchedLibraries = buildListOfChoosenLibrariesFromURL(searchedUrlPackages, allLibraryList);
        setListOfSearchedLibraries(searchedLibraries);
        
        if(localStorage.getItem(localStorageName)) {
          setListOfPinedLibraries(
            buildListOfPinnedLibrariesFromLocalStorage(localStorageName, searchedLibraries)
          )
        }
      }
    })
  }, [history, searchedUrlPackages]);

  const handleMenuClose = useCallback((event, index) => {
    const sortFn = [compareByFinalScore, compareByName, compareByDownloads, compareByStars][index];
    setListOfSearchedLibraries([...listOfSearchedLibraries.sort(sortFn)]);
    setListOfPinedLibraries([...listOfPinedLibraries.sort(sortFn)]);
    setFilterButtonAnchor(null);
}, [listOfSearchedLibraries, listOfPinedLibraries]);

  const addLibrary = useCallback((event) => {
    if(event.key !== 'Enter') return;

    setDuplicateEntry(false);

    if(checkDuplicateEntry(listOfSearchedLibraries, currentSearchFieldValue)) {
      setDuplicateEntry(true);
      setCurrentSearchFieldValue('');
      return;
    }

    if(listOfAllLibraries.some(el => el.name === currentSearchFieldValue)) {
      const list = [...listOfSearchedLibraries];
      list.push(listOfAllLibraries.filter((curr) => curr.name === currentSearchFieldValue)[0]);
      setListOfSearchedLibraries(list);
      selectedPackagesForURL(list, history);
      setCurrentSearchFieldValue('');
      return;
    }

    if(currentSearchFieldValue) {
      setLoading(true);
      searchNewPackages(currentSearchFieldValue).then(response => {
        if(response === 'done') {
          setLoading(false);
          setSnackbarOpen(true);
          setSnackbarSeverity('success');
          setSnackbarMessage('Please press the enter key in the search bar again to add the library to your selected libraries');
        }else {
          setLoading(false);
          setSnackbarOpen(true);
          setSnackbarSeverity('error');
          setSnackbarMessage(currentSearchFieldValue + ' was not found in the npm API! Check if you spelled the library right.');
        }
      })
    }
  }, [listOfAllLibraries, currentSearchFieldValue, listOfSearchedLibraries, history]);

  const handledropLibrary = useCallback((nameOfLibrary) => {
    if(listOfSearchedLibraries.length === 1) {
      setListOfSearchedLibraries([]);
      setListOfPinedLibraries([]);
      selectedPackagesForURL([], history);
      return;
    }

    const choosenList = [...listOfSearchedLibraries];
    const pinedList = [...listOfPinedLibraries];
    const indexOfPinedListItem = pinedList.map((e) => {return e.name}).indexOf(nameOfLibrary);
    const indexOfChoosenListItem = choosenList.map((e) => {return e.name}).indexOf(nameOfLibrary);

    if(indexOfPinedListItem !== -1) {
      pinedList.splice(indexOfPinedListItem, 1);
      setListOfPinedLibraries(pinedList);
      localStorage.setItem(localStorageName, JSON.stringify(pinedList));
    }

    choosenList.splice(indexOfChoosenListItem, 1);
    setListOfSearchedLibraries(choosenList);
    selectedPackagesForURL(choosenList, history);
  }, [history, listOfSearchedLibraries, listOfPinedLibraries]);

  const addPinedLibrary = useCallback((index) => {
    const pinedList = [...listOfPinedLibraries];
    pinedList.push(listOfSearchedLibraries[index]);
    setListOfPinedLibraries(pinedList);
    localStorage.setItem(localStorageName, JSON.stringify(pinedList));
  }, [listOfPinedLibraries, listOfSearchedLibraries]);

  const dropPinedLibrary = useCallback((index) => {
    const pinedList = [...listOfPinedLibraries];
    pinedList.splice(index, 1);
    setListOfPinedLibraries(pinedList);
    localStorage.setItem(localStorageName, JSON.stringify(pinedList));
  }, [listOfPinedLibraries]);

  return (
    <>
      <Bar />
      
      <div style={{margin: '100px 10%'}}>
        <div style={{display: 'flex', justifyContent: 'center'}}>
          <Autocomplete 
            style={{width: '50%'}}
            options={listOfAllLibraries}
            getOptionLabel={(option) => option.name}
            inputValue={currentSearchFieldValue}
            onInputChange={(event, value) => setCurrentSearchFieldValue(value)}
            onChange={(event, value) => value ? setCurrentSearchFieldValue(value.name) : console.log()}
            noOptionsText='click enter to search this library'
            renderInput={params => 
              <TextField
                {...params}
                style={{marginBottom: 20}}
                error={duplicateEntry}
                label={duplicateEntry ? 'Entry already exists' : 'search library'}
                type='text'
                placeholder={duplicateEntry ? 'search library' : ''}
                rowsMax='1'
                margin='normal'
                variant='outlined'
                onKeyDown={addLibrary}
              />
            }
          />

          <Tooltip title='filter cards'>
            <IconButton 
              color='primary'
              onClick={event => setFilterButtonAnchor(event.currentTarget)}
              size='medium'
              style={{margin: 20}}
            >
              <FilterListIcon />
            </IconButton>
          </Tooltip>
          <Menu
            anchorEl={filterButtonAnchor}
            keepMounted
            open={Boolean(filterButtonAnchor)}
            onClose={handleMenuClose}
          >
            {filterOptions.map((option, index) => (
              <MenuItem 
                key={index}
                onClick={event => handleMenuClose(event, index)}
              >
                {option}
              </MenuItem>
            ))}
          </Menu>
        </div>

        {loading ? (
          <>
            <div style={{marginTop: '50px'}}><LinearProgress variant='query' /></div>
            <div className={classes.footer}>
                <Footer />
            </div>
          </>
        ) : (
          listOfSearchedLibraries.length > 0 ? (
            <>
              <Grid container spacing={2}>
                {listOfPinedLibraries?.map((curr, index) => (
                  <Grid key={index} item xs={12} sm={12} md={6} lg={4}>
                    <CompareCard 
                      cardData={curr} 
                      dropLibraryFunc={() => handledropLibrary(curr.name)}
                      pined={true}
                      pinFunc={() => dropPinedLibrary(index)}
                    />
                  </Grid>
                ))}
              
                {listOfSearchedLibraries.map((curr, index) => 
                  !listOfPinedLibraries.some(el => el.name === curr.name) ? (
                    <Grid key={index} item xs={12} sm={12} md={6} lg={4}>
                      <CompareCard 
                        cardData={curr} 
                        dropLibraryFunc={() => handledropLibrary(curr.name)}
                        pined={false}
                        pinFunc={() => addPinedLibrary(index)}
                      />
                    </Grid>
                  ): null
                )}
              </Grid>
              <div style={{marginTop: '5%'}}>
                <Footer />
              </div>
            </>
          ):(
            <>
              <Typography variant='h4' style={{textAlign: 'center'}}>
                <Box fontWeight='fontWeightBold'>
                    No libraries selected
                </Box>
              </Typography>

              <div className={classes.footer}>
                <Footer />
              </div>
            </>
          )
        )}

        <ErrorSnackbar 
          open={snackbarOpen} 
          severty={snackbarSeverity} 
          message={snackbarMessage} 
          handleCloseSnackbar={(reason) => {
            if (reason === 'clickaway') return;
              setSnackbarOpen(false);
            }} 
        />
      </div>
    </>
  );
}

export default Compare;
